body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: auto;
  table-layout: fixed;

  position: absolute;
}
button{
  color: #007492;
  text-decoration: none;
  background-color:transparent;
  display:inline-block;
  border:0px;
  cursor:pointer;

}


.board{
  width:1000px;
}
.status {
  margin-bottom: 10px;
  font-size:50px;
  text-align:center;
  color: #007492;
}
.substatus {
  margin-bottom: 10px;
  margin-top:20px;
  font-size:20px;
  text-align:center;
  color: #007492;
}
.boardrow{
  position:absolute;
  table-layout: fixed;
}
.square {
  background: #fff ;
  background-size: cover;
  background-position: center;
  border: 2px solid #007492;
  border-collapse: collapse;
  height: 100px;
  width: 200px;
  overflow: hidden;

}
.check-box{
  position:relative
}

.square h2{
  margin-bottom: 10px;
  font-size:200%;
  color: #FFF;
  text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game-info {
  margin-left: 20px;
}

.new-game-button{
  margin-left: 350px;
  margin-right:50px;
  width:175px;
  align:center;
  text-align:center;
  display:inline-block;
  background-color: rgba(0, 116, 146, 0.33);
  border-radius:10px;
}

.print-button{
  position:relative;
  display:inline-block;
  background-color: rgba(0, 116, 146, 0.33);
  border-radius:10px;
  align:center;
  text-align:center;
  width:175px;
}



@media print
{

  * {-webkit-print-color-adjust:exact; }
  .new-game-button {display: none;}
  .print-button {display: none;}
  .check-box {display: none;}
  head {display: none;}


  .square h2{
    -webkit-filter: drop-shadow(-1px 0 black) drop-shadow(0 1px black) drop-shadow(1px 0 black) drop-shadow(0 -1px black);
    text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
  }


}
@page { size: auto;  margin-left: 10mm; margin-right:10mm; margin-top: 0mm; margin-bottom: 0mm }




.user1290{
  clear : none;
  float : left;
  position : relative;
  width : 50px;
  height : 100px;
}


